@keyframes rotate-yinyang-icon {
  from {
    transform: rotate(0deg);
  } to {
    transform: rotate(-360deg);
  }
}
@keyframes show-intro-box {
  from {
    height: 0;
  }
  to {
    height: 55vh;
  }
}
@keyframes fade-in {
  0%, 50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes spaceman-img-move {
  0%, 100% {
    transform: translate(-15px, -15px);
  }
  50% {
    transform: translate(20px, 20px);
  }
}
@keyframes spaceman-mounted {
  from {
    transform: translate(400px, 500px);
  }
  to {
    transform: translate(20px, 20px);
  }
}
@keyframes social-icons {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.7);
  }
}
@keyframes blog-box {
  0%, 50% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes line-social {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}
@keyframes page-move {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
